@import url(https://fonts.googleapis.com/css2?family=Spartan:wght@500;700&display=swap);
:root{
  --Dark_Cyan: hsl(180, 29%, 50%);
  --Cyan_Background: hsl(180, 52%, 96%);
  --Cyan_Filter_Tablets: hsl(180, 31%, 95%);
  --Dark_Grayish_Cyan: hsl(180, 8%, 52%);
  --Very_Dark_Grayish_Cyan: hsl(180, 14%, 20%);
}
*,html{
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}
body {
  font-family: 'Spartan', sans-serif;
  font-size: 15px;
  width: 100%;
  min-height: 100vh;
  background-color: hsl(180, 52%, 96%);
  background-color: var(--Cyan_Background);
}
ul{
  list-style-type: none;
}
code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

header{
    display: block;
    height: 160px;
    background-image: url(/Static_job_list/static/media/bg-header-desktop.8694f466.svg);
    background-repeat: repeat-x;
    background-size: cover;
    background-position: center;
    background-color: var(--Dark_Cyan);
    margin-bottom: 80px;
}
@media (max-width:375px){
    header{
        background-image: url(/Static_job_list/static/media/bg-header-mobile.9cb13612.svg);
    }
}
.list_job{
    display: block;
    max-width: 1440px;
    margin: 0 auto;
}
.list_job li{
    display: flex;
    flex-direction: row;
    align-items: center;
    width: 77%;
    margin: 0 auto 24px auto;
    border-radius: 4px;
    box-shadow: 0px 5px 10px hsla(180, 28.6%, 50%, 0.37);
    background-color: #FFF;
    padding: 28px 40px;
    min-height: 152px;
    cursor: default;
}
.list_job .left{
    width: 88px;
    height: 88px;
    flex-shrink: 0;
    margin-right: 25px; 
}
.list_job .left img{
    width: 100%;
}
.center{
    max-width: 70%;
    flex-shrink: 0;
}
.center_header{
    margin: 0px 0 10px 0;
    min-height: 25px;
}
.center_header, .center_footer{
    display: flex;
    flex-direction: row;
    align-items: center;
    width: 100%;
    font-size: 14px;
    font-weight: 700;
}
.center_header > p{
    color: var(--Dark_Cyan);
    font-weight: 700;
}
.center_header > span{
    color: #FFF;
    font-size: 12px;
    border-radius: 15px;
    margin-left: 13px;
    padding: 0 7px;
    display: block;
    height: 24px;
    line-height: 28px;
}
.center_header .new{
    background-color: var(--Dark_Cyan);
}
.center_header .featured{
    background-color: var(--Very_Dark_Grayish_Cyan);
}
.center_body{    
    margin-bottom: 10px;
}
.center_body > p{
    color: var(--Very_Dark_Grayish_Cyan);
    font-weight: 700;
    font-size: 18px;
    cursor: pointer;
}
.center_body > p:hover{
    color: var(--Dark_Cyan)
}
.center_footer > p{
    color: var(--Dark_Grayish_Cyan);
}
.center_footer > p::after{
    display: inline;
    content: "·";
    margin: 0 15px;
}
.center_footer > p:last-child::after{
    content: "";
    margin: 0;
}
.right{
    display: flex;
    flex-direction: row;
    width: 100%;
    justify-content: flex-end;
    flex-shrink: 1;
    flex-wrap: wrap;
}
.right span{
    color: var(--Dark_Cyan);
    font-size: 12px;
    border-radius: 2px;
    padding: 0 7px;
    display: block;
    height: 24px;
    line-height: 25px;
    background-color: var(--Cyan_Filter_Tablets);
    cursor: pointer;
    -webkit-user-select: none;
        -ms-user-select: none;
            user-select: none;
    margin: 7px;
}
.right span:hover{
    color: #FFF;
    background-color: var(--Dark_Cyan);
}




@media (max-width:850px){
    .list_job li{
        flex-wrap: wrap;
        width: 90%;
        padding: 20px;
    }
    .center{
        max-width: 100%;
    }
    .right{
        margin-top: 25px;
        padding-top: 10px;
        justify-content: space-around;
        box-shadow: 0 -1px 0 0 var(--Dark_Grayish_Cyan);
    }
}
@media (max-width:375px){
    .list_job li{
        margin-bottom: 48px;
    }
    .list_job .left{
        width: 50px;
        height: 50px;
        margin-top: -42px;
        margin-bottom: 5px;
    }
}
.cont_filter{
    display: block;
    max-width: 1440px;
    margin: -114px auto 46px auto;
}
.cont_filter ul{
    display: flex;
    flex-direction: row;
    align-items: center;
    margin: 0 auto;
    width: 77%;
    border-radius: 4px;
    box-shadow: 0px 5px 10px hsla(180, 28.6%, 50%, 0.37);
    background-color: #FFF;
    padding: 15px 40px;
    min-height: 52px;
    cursor: default;
    justify-content: space-between;
}
.cont_filter ul li:first-child{
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    align-items: center;
    width: 100%;
    flex-shrink: 1;
}
.cont_filter ul li div{
    display: flex;
    flex-direction: row;
    flex-wrap: nowrap;
    align-items: center;
    height: 24px;
    background-color: var(--Cyan_Filter_Tablets);
    border-radius: 2px;
    font-size: 12px;
    margin: 7px;
}
.cont_filter ul li p{
    color: var(--Dark_Cyan);
    padding: 0 7px;
    line-height: 24px;
    cursor: default;
    -webkit-user-select: none;
        -ms-user-select: none;
            user-select: none;
}
.cont_filter ul li > p:hover{
    text-decoration: underline;
    cursor: pointer;
}
.cont_filter ul li span{
    width: 24px;
    height: 100%;
    background-image: url(/Static_job_list/static/media/icon-remove.70c075ce.svg);
    background-position:center;
    background-repeat: no-repeat;
    background-size: auto;
    background-color: var(--Dark_Cyan);
    cursor: pointer;
}
.cont_filter ul li span:hover{
    background-color: var(--Very_Dark_Grayish_Cyan);
}

@media (max-width:850px){
    .cont_filter ul{
        width: 90%;
        padding: 20px;
    }
}
